.movieContainer{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 50px;
}

.singleMovie{
    background-color: rgba(255, 255, 255, 0.178);
    display: flex;
    flex-direction: column;
    height: 430px;
    width: 250px;
    margin: 25px;
    padding: 5px;
    border-radius: 5px;
    box-shadow: 0 0 5px 2px lightgray;
    cursor: pointer;
    position: relative;
}

.movieCover{
    overflow: hidden;
}

.movieCover > img{
    height: 350px;
    width: 100%;
    border-radius: 5px;
    transition: .2s;    
}


.movieCover > img:hover{
    transform: scale(1.3);
    filter: blur(2px);
    opacity: .8;
}


.movieInfo{
    color: white;
    text-shadow: 1px 2px 3px black;
    letter-spacing: 1px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 5px;
    position: absolute;
    bottom: 5px;
    right: 5px;
    left: 5px;
}

.movieName{
    margin-bottom: 25px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-weight: bold;
}

.rateAndYear{
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.emptyField{
    box-shadow: 0 0 2px 2px grey;
    padding: 50px;
    border-radius: 10px;
    font-size: 3rem;
}

.eye{
    font-size: 5rem;
    position: absolute;
    left: 90px;
    top: 150px;
    color: white;
}